import { Component, OnInit, Input } from '@angular/core';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent implements OnInit {
  title = 'app';
  baseRate = 102;
  startDate;
  people = 2;
  calculatedCost = 0;

  ngOnInit(): void {
    this.cost();
    this.startDate = new Date() + '';
  }

  cost(): void {
    setInterval(() => {
      try {
        const elapsed = Math.round(new Date().getTime() - ((new Date(this.startDate)).getTime()));
        this.calculatedCost = (elapsed / 1000) * (this.people * (this.baseRate / 3600));
      } catch (err) {
        console.log('Invalid time', err);
        this.calculatedCost = 0;
      }
    }, 1000);
  }
}



